	module.exports = {"data":{"nodePost":{"title":"jQuery and Drupal 8.4 Upgrades","link":null,"body":{"value":"<p>Learned the hard way recently that Drupal 8.4 upgrades jQuery to jQuery 3.  This kills a few methods that were deprecated in previous versions of jQuery including .load(), .unload(), .error(), and .size().  The <a href=\"https://www.drupal.org/project/drupal/releases/8.4.0\">release notes</a> cover this, but it didn't fully click until I actually ran into the issue.</p>\r\n<p>Related: is a minor release really a minor release if it contains major release upgrades for a couple of your largest dependencies?</p>"},"relationships":{"image":null},"created":1519075633}},"pathContext":{"slug":"/note/jquery-and-drupal-8.4-upgrades/"}}

